body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #113d34;/* Еще более темный синий цвет для фона */
  color: #FFFFFF; /* Белый цвет для текста */
}
.app{
  background-color: #111515;
}

.sidebar-container {
  display: flex;
  flex-direction: column;
  background-color: #09121f; /* Темно-темно-синий цвет для боковой панели */
  color: #FFFFFF; /* Белый цвет для текста */
  transition: width 0.2s;
}

.toggle-button {
  background-color: #F27245; /* Оранжевый цвет для кнопки */
  color: #FFFFFF; /* Белый цвет для текста кнопки */
  border: none;
  padding: 10px;
  cursor: pointer;
  text-align: left;
}

.header {
  background-color: #000000; /* Темно-синий цвет для верхней панели */
  color: #FFFFFF; /* Белый цвет для текста */
  padding: 20px;
  text-align: center;
  margin-bottom: 10px; /* Добавляем небольшой отступ между шапкой и кнопкой */
}

.title {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;
}

.sidebar {
  width: 200px;
  overflow: hidden;
  transition: width 0.2s;
}

.sidebar-enter {
  width: 0;
}

.sidebar-enter-active {
  width: 200px;
}

.sidebar-exit {
  width: 200px;
}

.sidebar-exit-active {
  width: 0;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  height: calc(100vh - 100px); /* Учитываем высоту шапки и кнопки Show/Hide */
}

.sidebar ul li {
  padding: 10px;
  cursor: pointer;
}
.sidebar{
  background-color: #0e0e10;
}

.sidebar ul li:hover {
  background-color:#101013; /* Темный синий цвет для выделения */
}

.file {
  color: #FFFFFF; /* Белый цвет для текста */
}

.directory {
  color: #9CDCFE; /* Голубой цвет для папок */
}

button {
  background-color: #F27245; /* Оранжевый цвет для кнопки */
  color: #FFFFFF; /* Белый цвет для текста кнопки */
  border: none;
  padding: 10px;
  cursor: pointer;
  width: 100%;
  text-align: left;
}

button:disabled {
  background-color: #4A4A4A; /* Темно-серый цвет для неактивных кнопок */
  cursor: not-allowed;
}

.main-content {
  flex: 1;
  padding: 20px;
  background-color: #16161c; /* Темно-темный синий цвет для основного контента */
  color: #FFFFFF; /* Белый цвет для текста */
  overflow-y: auto;
}

.main-content img {
  max-width: 100%;
  height: auto;
}

.main-content pre {
  white-space: pre-wrap;
}

.main-content table {
  width: 100%;
  border-collapse: collapse;
}

.main-content table, .main-content th, .main-content td {
  border: 1px solid #FFFFFF; /* Белый цвет для границ таблицы */
}

.main-content th, .main-content td {
  padding: 8px;
  text-align: left;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #113d34;
}

::-webkit-scrollbar-thumb {
  background-color: #FFA500;
  border-radius: 20px;
  border: 3px solid #113d34;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #FFD700;
}