/* DirectoryViewer Styles */
.directory-viewer {
    display: flex;
    flex-wrap: wrap;
    gap: 30px; /* Increased gap */
    padding: 20px;
    background-color: #112220;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%; /* Adjusted width */
    box-sizing: border-box; /* Include padding and border in the element's total width and height */
  }
  
  .directory-container {
    flex: 1 1 100%;
    background-color: #0d1a17;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .directory-v {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .directory-v .icon {
    font-size: 24px;
    color: #ffa500;
    margin-bottom: 5px;
  }
  
  .directory-v span {
    font-size: 14px; /* Adjusted font size */
    color: #ffa500;
  }
  
  .files-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr)); /* Adjusted min-width for more space */
    gap: 20px; /* Increased gap */
    width: 100%;
  }
  
  .file-item {
    background-color: #1c1c1c;
    padding: 15px; /* Added padding */
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: background-color 0.3s;
    text-align: center; /* Center-align text */
  }
  
  .file-item:hover {
    background-color: #333;
  }
  
  .file-item .icon {
    font-size: 24px;
    color: #ffa500;
    margin-bottom: 10px; /* Increased margin for better spacing */
  }
  
  .file-item span {
    font-size: 12px; /* Adjusted font size */
    color: #ffa500;
    word-wrap: break-word; /* Allow long words to wrap */
    width: 100%; /* Ensure the text spans the width of the item */
  }
  