/* Loading Screen Styles */
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #194b34;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  overflow: hidden;
  animation: background-slide 4s infinite alternate;
}

.logo-image-container {
  position: relative;
  width: 375px;
  height: 375px;
  margin-bottom: 20px;
  animation: logo-bounce 2s infinite;
}

.logo-image {
  background-color: #FFA500;
  width: 100%;
  height: 100%;
  object-fit: contain;
  box-shadow: 0 0 10px rgb(255, 199, 32);
}

.logo-text {
  font-family: 'Bebas Neue', cursive;
  font-size: 48px;
  color: #FFA500;
  background: linear-gradient(90deg, rgba(255,165,0,1) 0%, rgba(255,215,0,1) 50%, rgba(255,165,0,1) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 20px;
  animation: text-fade-in 2s ease-in-out;
}

@media (max-width: 768px) {
  .logo-text{
    font-size: 20px;
  }
  .logo-image-container{
    width: 180px;
    height: 180px;
  }
}

.loading-bar-container {
  width: 37%;
  background-color: #fff9ec;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.loading-bar {
  width: 0;
  height: 10px;
  background-color: #ffe1aa;
  animation: loading 1.5s forwards;
}

/* Keyframes */
@keyframes loading {
  from {
    width: 0;
    background-color: #ffe89e;

  }
  to {
    width: 100%;
    background-color: #FFA500;;
  }
}

@keyframes logo-bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes text-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes background-slide {
  from {
    background-color: #2a643d;
  }
  to {
    background-color: #151419;
  }
}
