/* Import custom font */
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

/* Header Styles */
.header {
  background-color:  #1a4d28;
  color: hsl(39, 100%, 59%);
  padding: 20px 30px;
  box-shadow: 0 4px 8px rgba(37, 31, 45, 0.1);
  position: sticky;
  top: 0;
  z-index: 1002;
  border-bottom: 2px solid #FFA500;
  /* width: 100%; */
  height: 7vh;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 100vw;
  margin: 0 auto;
}

.logo {
  display: flex;
  align-items: center;
}

.logo-icon {
  font-size: 50px;
  margin-right: 20px;
  color: #ffd015;
  animation: spin 4s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.title {
  font-family: 'Bebas Neue', cursive;
  font-size: 42px;
  font-weight: bold;
  color: rgb(255, 190, 69);
  text-transform: uppercase;
  letter-spacing: 2px;
  background: linear-gradient(90deg, rgba(255,165,0,1) 0%, rgba(255,215,0,1) 50%, rgba(255,165,0,1) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.dev-mode-button {
  background-color: #FFA500;
  color: #1E1E1E;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 8px;
  width: 150px;
  display: flex;
  align-items: center;
  margin-right: 0; /* Ensure button aligns to the right */
  transition: background-color 0.3s, transform 0.3s;
}

.dev-mode-button svg {
  margin-right: 8px;
}

.dev-mode-button:hover {
  background-color: #ffd4a0;
  transform: scale(1.05);
}

.dev-mode-button:focus {
  outline: none;
  box-shadow: 0 0 5px #FFD700;
}

@media (max-width: 768px) {
  .title{
    font-size: 20px;
  }
  .logo-icon{
    font-size: 35px;
  }
  .dev-mode-button{
    font-size: 14px;
  }

}
